import Vue from 'vue'
import VueScroller from 'vue-scroller'
import pageHeader from '../../../components/pageHeader'
import netBroken from '../../../components/ErrorPage/netBroken'
import { myJpointHeadApi, myJpointDetailApi } from '@/api/surprise'
import { isAndroid, isIOS } from '@/utils/platform'
Vue.use(VueScroller)
export default {
  name: 'mine',
  data () {
    return {
      isNetError: false,
      tabs: ['全部', '收入', '支出'],
      activeIndex: 0,
      scrollerHeight: 0,
      cntJpoint: 0,
      cntJpointStr: '',
      info: {
        allInfoNum: 1,
        inComeNum: 1,
        expenditureNum: 1,
        allInfoDetail: [],
        inComeNumDetail: [],
        expenditureDetail: []
      },
      isShow: false,
      types: ['allInfoNum', 'inComeNum', 'expenditureNum'],
      detailTypes: ['allInfoDetail', 'inComeNumDetail', 'expenditureDetail'],
      fromWay: true,
      firstGetDataOver: false
    }
  },
  components: {
    pageHeader,
    netBroken
  },
  methods: {
    refresh (done) {
      done()
    },
    // 下拉刷新
    infinite (done) {
      const type = this.activeIndex
      this.getIntegralDetail(type, done)
    },
    clickItem (index) {
      this.activeIndex = index
      this.$nextTick(() => {
        const scroller = this.$refs.scroller
        scroller && scroller.scrollTo(0, 0)
      })
    },
    getScrollerHeight () {
      const integral = this.$refs.integral.offsetHeight
      const tabs = this.$refs.tabs.offsetHeight
      const clientHeight = document.body.offsetHeight
      const pageHeader = this.$refs.pageHeader.$el.offsetHeight
      let diff = clientHeight - integral - tabs - pageHeader
      this.scrollerHeight = diff
    },
    getIntegral () {
      myJpointHeadApi({uid: this.uid}).then(res => {
        console.log('res', res)
        if (res.status == 200 && res.data.code == 2000) {
          this.cntJpoint = res.data.data.cntJpoint
          this.cntJpointStr = res.data.data.cntJpointStr
        }
      })
    },
    getIntegralDetail (type, done) {
      const props = {
        uid: this.uid,
        pageNum: this.info[this.types[type]],
        type: type
      }
      myJpointDetailApi(props).then(res => {
        this.info[this.types[type]] += 1
        if (res.status == 200 && res.data.code == 2000) {
          const data = res.data.data
          data.forEach((item, index) => {
            if (item.jpoint > 0) {
              data[index].jpointStr = `+${item.jpointStr}`
            }
          })
          if (data.length == 0) {
            done && done('finishInfinite')
            this.$nextTick(() => {
              this.detail.length == 0 && (this.firstGetDataOver = true)
            })
            return
          }
          const detailType = this.detailTypes[type]
          this.info[detailType].push(...res.data.data)
          done && done()
        }
      })
    },
    jump () {
      this.$router.push({
        path: '/credits/rule',
        query: {
          from: 'h5'
        }
      })
    },
    goBack () {
      if (this.fromWay) {
        if (isAndroid()) {
          window.glamor.closePage()
          return
        }
        if (isIOS()) {
          this.$device.JsBridge.callHandler('h5LeftBack', {}, (data) => {})
          return
        }
        this.$router.go(-1)
        return
      }
      this.$router.go(-1)
    }
  },
  created () {
    this.isNetError = !window.navigator.onLine
    if (this.isNetError) {
      return
    }
    this.getIntegral()
    this.tabs.forEach((item, index) => {
      this.getIntegralDetail(index)
    })
  },
  computed: {
    uid () {
      return this.$route.query.uid
    },
    detail () {
      return this.info[this.detailTypes[this.activeIndex]]
    }
  }

}



